<template>
    <Page>
        <Header>
            <Navbar ref="navbar" color="primary">
                <Title ref="title" @onTitleClick="onTitleClickHandler">API Test</Title>
                <Buttons right slot="buttons">
                    <Button color="dark" @click="personHandler" role="bar-button">
                        <Icon name="icon-vue"></Icon>
                    </Button>
                    <Button @click="showPopMenu" role="bar-button">
                        <Icon name="icon-more"></Icon>
                    </Button>
                </Buttons>
            </Navbar>
        </Header>
        <Content padding class="outer-content">
            <h5>
                当前平台：{{platformName}}
            </h5>
            <p>Vimo框架会识别当前的平台 <strong>根据平台提供的JSSDK开启特定平台的组件</strong>, 比如下方列出的组件会根据平台展现不同的特性, <strong>默认使用H5模式</strong>, 目前支持Alipay和Dingtalk两个平台, 你可以试下在这两个平台打开这个页面体验下面的设置方法. 当然, 下面列出的方法只是部分方法...
            </p>

            <h5 text-danger class="text-danger">弹窗组件</h5>
            <Button block @click="openToast">打开Toast</Button>
            <Button block @click="openActionSheet">打开ActionSheet</Button>
            <Button block @click="openAlert">打开Alert</Button>
            <Button block @click="openConfirm">打开Confirm</Button>
            <Button block @click="openPrompt">打开Prompt</Button>
            <Button block @click="openLoading">打开Loading</Button>

            <h5>导航栏</h5>
            <Button block @click="btnTitle">设置标题文字</Button>
            <Button block @click="btnImage">设置标题图片(url)</Button>
            <Button block @click="showOptionButton">显示右侧按钮</Button>
            <Button block @click="hideOptionButton">隐藏右侧按钮</Button>
            <Button block @click="btnReset">重置导航栏</Button>

            <h5>导航栏菜单</h5>
            <Button block @click="showPopMenu">显示右侧按钮菜单</Button>
            <h5>选择器Picker</h5>

            <p>选择的数据: {{smoothie}}</p>
            <Button block @click="twoColumns">双列Picker</Button>

            <h5>图片预览</h5>
            <Button block @click="openAlbum">图片预览</Button>

        </Content>
    </Page>
</template>
<style scoped lang="scss">
    .icon-vue {
        height: 30px;
        width: 30px;
        box-sizing: content-box;
        background: url('http://cn.vuejs.org/images/logo.png') no-repeat left center/30px 30px;
    }

    .icon-more {
        height: 30px;
        width: 30px;
        box-sizing: content-box;
        background: url('https://avatars0.githubusercontent.com/u/26055200?v=3&s=200') no-repeat left center/30px 30px;
    }

    .icon-vimo {
        height: 30px;
        width: 30px;
        background: url('../assets/img/vimo.png') no-repeat center center/30px 30px;
    }

    .icon-alipay {
        height: 30px;
        width: 40px;
        background: url('../assets/img/alipay.png') no-repeat center center/cover;
    }

    .icon-wechat {
        height: 30px;
        width: 36px;
        background: url('../assets/img/wechat.png') no-repeat center center/cover;
    }
</style>
<script type="text/javascript">
  import { Badge } from 'vimo/components/badge'
  import { Picker } from 'vimo/components/picker'
  import { PreviewImage } from 'vimo/components/preview-image'
  export default{
    name: 'name',
    data () {
      return {
        platformName: 'H5',
        smoothie: `Banana Watermelon`
      }
    },
    props: {},
    watch: {},
    computed: {
      titleComponent () {
        return this.$refs.title
      },
      navbarComponent () {
        return this.$refs.navbar
      }
    },
    methods: {
      personHandler () {
        this.$alert.present({
          title: '快上车',
          message: '老司机要开车了, 系好安全带!',
          buttons: [
            {
              text: '系好了',
              handler: () => {
                console.log('系好了 clicked')
              }
            }
          ]
        })
      },
      /**
       * 弹窗组件
       * */
      openToast () {
        this.$toast({
          type: 'success',
          message: '支付成功',
          duration: 3000,
          onDismiss () {
            console.debug('Toast 关闭')
          }
        })
      },
      openActionSheet () {
        this.$actionSheet.present({
          title: '请选择操作',
          buttons: [
            {
              text: '再弹一次',
              role: 'destructive',
              handler: () => {
                console.log('再弹一次 clicked')
                this.$actionSheet.present({
                  title: '请选择操作',
                  buttons: [
                    {
                      text: '苹果',
                      handler: () => {
                        console.log('苹果 clicked')
                      }
                    },
                    {
                      text: '柠檬',
                      handler: () => {
                        console.log('柠檬 clicked')
                      }
                    },
                    {
                      text: '土豆',
                      handler: () => {
                        console.log('土豆 clicked')
                      }
                    },
                    {
                      text: '关闭',
                      role: 'cancel',
                      handler: () => {
                        console.debug('关闭 clicked')
                      }
                    }
                  ]
                })
              }
            },
            {
              text: '翻转',
              handler: () => {
                console.log('翻转 clicked')
              }
            },
            {
              text: '增加',
              handler: () => {
                console.log('增加 clicked')
              }
            },
            {
              text: '取消',
              role: 'cancel',
              handler: () => {
                console.log('取消 clicked')
              }
            }
          ]
        }).then(function () {
          console.log('actionsheet 开启 promise')
        })
      },
      openAlert () {
        this.$alert.present({
          title: '温馨提示',
          message: '您的快递到了！',
          buttons: [
            {
              text: '确定',
              handler: () => {
                console.log('确定 clicked')
              }
            }
          ]
        })
      },
      openConfirm () {
        this.$alert.present({
          title: '温馨提示',
          message: '您是否想查询快递单号：\n1234567890',
          buttons: [
            {
              text: '暂不需要',
              role: 'cancel',
              handler: () => {
                console.log('暂不需要 click')
              }
            },
            {
              text: '马上查询',
              handler: () => {
                console.log('马上查询 click')
              }
            }
          ]
        })
      },
      openPrompt () {
        this.$alert.present({
          title: '登录iTunes Store',
          message: '请输入您Apple ID"apple@icloud.com"的密码',
          enableBackdropDismiss: true,
          inputs: [
            {
              type: 'password',
              name: 'password',
              placeholder: '密码',
              value: ''
            }
          ],
          buttons: [
            {
              text: '取消',
              role: 'cancel',
              handler: () => {}
            },
            {
              text: '确定',
              handler: (value) => {
                this.$alert.present({
                  title: '请确认',
                  message: '您输入的信息：' + JSON.stringify(value),
                  cssClass: '',
                  enableBackdropDismiss: true,
                  buttons: [
                    {
                      text: '确定',
                      role: 'cancel',
                      handler: (value) => {
                        this.$alert.dismiss().then(function () {
                          console.log('Alert 确定 click')
                        })
                      }
                    }
                  ]
                }).then(function () {
                  console.log('Alert present promise')
                })
              }
            }
          ]
        }).then(function () {
          console.log('Input present!')
        })
      },
      openLoading () {
        this.$loading.present({
          content: '正在加载'
        })

        window.setTimeout(() => {
          this.$loading.dismiss('loading.dismiss')
        }, 3000)
      },

      /**
       * 导航栏部分
       * */
      btnTitle () {
        this.titleComponent.setTitle('Hello Vimo', true)
      },
      btnImage () {
        this.titleComponent.setTitle({
          image: 'https://zos.alipayobjects.com/rmsportal/jFCQxeOXeKTQUfhrQFOo.png'
        })
      },
      btnReset () {
        this.titleComponent.setTitle('重置导航栏样式', true)
        this.navbarComponent.reset()
        this.titleComponent.reset()
      },
      showPopMenu () {
        let data = [
          {
            title: '周边美食',
            icon: 'https://zos.alipayobjects.com/rmsportal/mzorSIxVEdkTuxumzzau.png',
            badge: '-1',
            handler () {
              console.log('index:0 选择了: 周边美食')
            }
          },
          {
            title: '购物攻略',
            icon: 'https://zos.alipayobjects.com/rmsportal/UoBNIZJosEXNQtAxCEUg.png',
            badge: '100',
            handler () {
              console.log('index:1 选择了: 购物攻略')
            }
          },
          {
            title: '摄影技巧',
            icon: 'https://zos.alipayobjects.com/rmsportal/QJeWMNUFFiDCQawMLPTr.png',
            badge: '12',
            handler () {
              console.log('index:2 选择了: 摄影技巧')
            }
          },
          {
            title: '搞笑段子',
            icon: 'data:image/png;base64,',
            badge: '0',
            handler () {
              console.log('index:3 选择了: 搞笑段子')
            }
          }
        ]
        this.navbarComponent.showPopMenu(data)
      },
      showOptionButton () {
        this.navbarComponent.showOptionButton()
      },
      hideOptionButton() {
        this.navbarComponent.hideOptionButton()
      },
      onTitleClickHandler () {
        alert('你点击了 platform-api.vue 页面的标题')
      },
      twoColumns () {
        let arr = this.smoothie.split(' ')

        let data = {
          buttons: [
            {
              text: '取消',
              role: 'cancel'
            },
            {
              text: '确定',
              handler: (data) => {
                this.smoothie = `${data.flavor1.value} ${data.flavor2.value}`
                console.log(data)
                console.log(data)
                console.log(JSON.stringify(data))
              }
            }
          ],
          columns: [
            {
              name: 'flavor1',
              align: 'right',
              selectedIndex: 1,
              options: [
                {text: 'Mango'},
                {text: 'Banana'},
                {text: 'Cherry'},
                {text: 'Strawberry'},
                {text: 'Raspberry'},
                {text: 'Blueberry'},
                {text: 'Peach'},
                {text: 'Coconut'},
                {text: 'Pineapple'},
                {text: 'Honeydew'},
                {text: 'Watermelon'},
                {text: 'Grape'},
                {text: 'Avocado'},
                {text: 'Kiwi'},
                {text: 'Orange'},
                {text: 'Papaya'}
              ]
            },
            {
              name: 'flavor2',
              align: 'left',
              selectedIndex: 3,
              options: [
                {text: 'Banana'},
                {text: 'Orange'},
                {text: 'Grape'},
                {text: 'Watermelon'},
                {text: 'Strawberry'},
                {text: 'Papaya'},
                {text: 'Kiwi'},
                {text: 'Cherry'},
                {text: 'Raspberry'},
                {text: 'Mango'},
                {text: 'Pineapple'},
                {text: 'Peach'},
                {text: 'Avocado'},
                {text: 'Honeydew'},
                {text: 'Blueberry'},
                {text: 'Coconut'}
              ]
            }
          ]
        }

        arr.forEach((item, index) => {
          let options = data.columns[index].options
          for (let i = 0, len = options.length; len > i; i++) {
            if (options[i].text === item) {
              data.columns[index].selectedIndex = i
            }
          }
        })

        Picker.present(data)
      },
      openAlbum () {
        PreviewImage({
          current: 1,
          urls: [
            'https://img.alicdn.com/tps/TB1sXGYIFXXXXc5XpXXXXXXXXXX.jpg',
            'https://img.alicdn.com/tps/TB1pfG4IFXXXXc6XXXXXXXXXXXX.jpg',
            'https://img.alicdn.com/tps/TB1h9xxIFXXXXbKXXXXXXXXXXXX.jpg'
          ]
        })
      }
    },
    mounted () {
      let platforms = this.$platform.platforms()
      if (platforms.length === 3) {
        this.platformName = platforms[2].toUpperCase()
      }
    },
    activated () {},
    deactivate () {},
    components: {Badge},
    destroyed () {}
  }
</script>



